/** Manages form data and validation rules */
export class FormModel {
    constructor(events, appState) {
        this.events = events;
        this.appState = appState;
        this.formErrors = {};
        this.payment = '';
        this.email = '';
        this.phone = '';
        this.address = '';
    }
    getPayment() {
        return this.payment;
    }
    getEmail() {
        return this.email;
    }
    getPhone() {
        return this.phone;
    }
    getAddress() {
        return this.address;
    }
    setPayment(value) {
        this.payment = value;
    }
    setEmail(value) {
        this.email = value;
    }
    setPhone(value) {
        this.phone = value;
    }
    setAddress(value) {
        this.address = value;
    }
    setOrderAddress(field, value) {
        if (field === 'address') {
            this.setAddress(value);
        }
        if (this.validateOrder()) {
            this.events.emit('order:ready', this.getOrderLot());
        }
    }
    validateOrder() {
        const errors = {};
        if (!this.getAddress()) {
            errors.address = '  ';
        }
        else if (!this.getPayment()) {
            errors.payment = '  ';
        }
        this.formErrors = errors;
        this.events.emit('formErrors:address', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    setOrderData(field, value) {
        if (field === 'phone') {
            let formattedPhone = value;
            // Allow plus sign only at the start
            if (formattedPhone.startsWith('+')) {
                formattedPhone = '+' + formattedPhone.slice(1).replace(/\D/g, '');
            }
            else {
                formattedPhone = formattedPhone.replace(/\D/g, '');
            }
            // Group digits for better readability
            if (formattedPhone.length > 0) {
                const groups = formattedPhone.match(/.{1,3}/g) || [];
                formattedPhone = groups.join(' ');
            }
            this.setPhone(formattedPhone);
        }
        else if (field === 'email') {
            this.setEmail(value);
        }
        if (this.validateContacts()) {
            this.events.emit('order:ready', this.getOrderLot());
        }
    }
    validateContacts() {
        const errors = {};
        if (!this.getEmail() || !this.getPhone()) {
            errors.contacts = '  email  ';
        }
        this.formErrors = errors;
        this.events.emit('formErrors:change', this.formErrors);
        return this.getEmail().length > 0 && this.getPhone().length > 0;
    }
    getOrderLot() {
        const cart = this.appState.getCart();
        return {
            payment: this.getPayment(),
            email: this.getEmail(),
            phone: this.getPhone(),
            address: this.getAddress(),
            total: cart.total,
            items: cart.items.map((item) => item.id),
        };
    }
    resetForm() {
        this.setPayment('');
        this.setEmail('');
        this.setPhone('');
        this.setAddress('');
        this.formErrors = {};
    }
}
